	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Brian Lonsdorf has a love-hate relationship with Underscore.js. Yes, it offers a bunch of tools included in today's functional programming paradigm (like map, filter, reduce, take, drop, compose, etc.), but in Underscore the functions are sometimes verbose and unintuitive. It claims to be a functional programming language, but how true is that?\n\nDespite certain characteristics, Brian offers some solutions for using Underscore productively. He plays a bit of catchup for the functional programming newbie, but then dives into the denser more theoretical concepts.\n\n**Follow along with Brian's slides: http://mrkn.co/5nvjz","meta":{"duration":"PT36M31S"},"presenter":{"name":null},"source":{"id":"m3svKOdZijA","provider":"youtube"},"slug":"hey-underscore-youre-doing-it-wrong","tag":["brian lonsdorf","html5","html5devconf","san francisco","Underscore","Underscore.js","functional programming","marakana","tips","tricks","use underscore.js productively"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/m3svKOdZijA/default.jpg","height":90,"width":120}},"title":"Hey Underscore, You're Doing It Wrong!"}}]}},"pathContext":{"slug":"san-francisco","tag":"san francisco"}}

